'use client';

import { Component, ReactNode } from 'react';
import { MESSAGE_TEMPLATES } from '@/lib/constants';

interface ErrorBoundaryProps {
  children: ReactNode;
  fallback?: ReactNode;
}

interface ErrorBoundaryState {
  hasError: boolean;
  error: Error | null;
}

/**
 * Error Boundary component
 * Catches JavaScript errors anywhere in the child component tree
 */
export default class ErrorBoundary extends Component<ErrorBoundaryProps, ErrorBoundaryState> {
  constructor(props: ErrorBoundaryProps) {
    super(props);
    this.state = { hasError: false, error: null };
  }

  static getDerivedStateFromError(error: Error): ErrorBoundaryState {
    return { hasError: true, error };
  }

  componentDidCatch(error: Error, errorInfo: React.ErrorInfo) {
    console.error('ErrorBoundary caught an error:', error, errorInfo);
  }

  render() {
    if (this.state.hasError) {
      if (this.props.fallback) {
        return this.props.fallback;
      }

      return (
        <div className="min-h-screen flex items-center justify-center bg-gray-900 p-4">
          <div className="max-w-md w-full bg-gray-800 rounded-lg shadow-xl p-8 text-center">
            <div className="text-6xl mb-4">‚ö†Ô∏è</div>
            <h1 className="text-2xl font-bold text-white mb-4">
              „Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü
            </h1>
            <p className="text-gray-300 mb-6">
              Áî≥„ÅóË®≥„Åî„Åñ„ÅÑ„Åæ„Åõ„Çì„ÄÇ‰∫àÊúü„Åó„Å™„ÅÑ„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ
            </p>
            {this.state.error && (
              <details className="text-left mb-6">
                <summary className="text-sm text-gray-400 cursor-pointer hover:text-gray-300 mb-2">
                  „Ç®„É©„ÉºË©≥Á¥∞
                </summary>
                <pre className="text-xs text-red-400 bg-gray-900 p-4 rounded overflow-auto max-h-40">
                  {this.state.error.message}
                </pre>
              </details>
            )}
            <button
              onClick={() => window.location.href = '/'}
              className="bg-[#06C755] hover:bg-[#05b34c] text-white px-6 py-3 rounded-lg transition w-full"
            >
              „Éõ„Éº„É†„Å´Êàª„Çã
            </button>
          </div>
        </div>
      );
    }

    return this.props.children;
  }
}
